*, *::after, *::before{
    box-sizing: border-box;
}
:root{
    --slider-padding:5rem;
}
.tv-main{
    margin-left: 10%;
}
.tv-body{ 
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
}

.tv-body .slider{
    --slider-index: 0;
    max-height: 250px;
    display: flex;
    flex-grow: 1;
    transform: translate(calc(var(--slider-index) * -100%));
    /* border: 2px solid white; */
}

.tv-body .slider img{
    object-fit: cover;
    max-width: 16%;
    aspect-ratio: 9/12;
    flex: 0 0 25%;
    padding: .20rem;
    border-radius: 10px;
}

.handle{
    width: 2.5em; height: 100%;
    margin: .15rem 0;
    z-index: 10;
    display: flex;justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 10px;
    border: none;
    background: transparent;
    color: white;
    transition: background-color 150ms ease-in-out;
    position: absolute;
    top: 0%;
}
