.carousel .carousel-inner .carousel-item img{
    width: 100%; height: 100vh;
}

.homePageContent{
    margin: 100px 0 0 120px; 
    color: white;
    position: relative;
    
}

.homePageContent .content-head{
    display: flex; justify-content: space-between; align-items: center;
    margin-top: 50px; padding-right: 20px;
    /* border: 2px solid red; */
}
.homePageContent .content-head h5{
    color: #E1E6F0;
    font-size: 24px; font-family: sans-serif;
    font-weight: 600;
}
.homePageContent .content-head .btn{
    color: rgb(58, 58, 58);
    font-weight: 500;
    font-size: 0.9rem;
    outline: none;
}
.homePageContent .content-head .btn:hover{
    color: #E1E6F0;
}
.content-body{
    width: 100%; height: 250px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
}
.content-body .contentList{
    max-height: 250px; width: 100%;
    display: flex;
    /* flex-grow: 1; */
}
.content-body .contentHandle{
    width: 5%; height: 100%;
    z-index: 10;
    display: flex;justify-content: center;
    align-items: center;
    cursor: pointer;    
    transition: 0.2s;   
    background: transparent;
    border: none;
    color: #e1e6f0;
}
.content-body .contentHandle span{
    visibility: hidden;
    font-size: 4em;
    transition: 0.2s;
}
.content-body .contentHandle:hover{
    background: rgba(11, 2, 2, 0.501);

}
.content-body .contentHandle:hover span{
    visibility: visible;
    transform: scale(1.1);
}
.content-body .contentList img{
    object-fit: cover;
    max-width: 20%;
    /* aspect-ratio: 9/12;
    flex: 0 0 25%; */
    padding: .20rem;
    border-radius: 10px;
}